@import '../mixins/index';
*,
::before,
::after {
  box-sizing: border-box;
}

body {
  background-repeat: no-repeat;
  margin: 0;
  font-family: map-get($fonts, r), -apple-system, BlinkMacSystemFont, 'Poppins',
    'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.28px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  display: block;
}
.shContainer {
  width: 100%;
  max-width: $maxWidthContent;
  margin: auto;
  box-sizing: border-box;
  @include maxLg() {
    padding: 0 16px;
    max-width: $maxWidthContentMobile;
  }
}
.spContainer {
  padding-top: 80px;
}
.Wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}
.sgContainer {
  padding: 70px 0 150px 0;
}
